.drawer-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  overflow: hidden;
}

.drawer {
  flex: 1;
  overflow: auto;
}

.drawer-app-content {
  flex: 3;
  overflow: auto;
}

.course-list-item :hover {
  cursor: pointer;
}

.fab {
  position: fixed;
  right: 4em;
  bottom: 4em;
  border-radius: 3em;
  width: 6em !important;
  height: 6em !important;
}

.schedule-select {
  width: 100%;
}
